import { Component, h, Host, JSX, Prop } from '@stencil/core';
import globalDataLayerService from '../../services/analytics/global-data-layer-service';
import { ClickElementTypes, ElementTypes, EventActions } from '../../utility/constants';
import analyticsManager from '../../services/analytics/analytics-manager';
import { getCurrentRoute } from '../../services/routing-manager';

@Component({
  tag: 'phn-back-button',
  styleUrl: 'back-button.scss',
  scoped: true
})
export class BackButton {
  @Prop() text = '';
  @Prop() analyticsId = '';

  private sendDataLayerEvent() {
    globalDataLayerService.pushDataLayerEvent(EventActions.MENU_BACK_CLICK, {
      componentClick: {
        clickElementId: this.analyticsId,
        clickElementName: this.text,
        clickElementType: ClickElementTypes.Button
      },
      componentDisplay: {
        displayElementId: analyticsManager.calculateDisplayElementId(),
        displayElementType: analyticsManager.calculateDisplayElementType(
          getCurrentRoute(),
          ElementTypes.backButton,
          true
        ),
        displayElementName: this.text
      }
    });
  }

  render(): JSX.Element {
    return (
      <Host>
        <phn-p-button-pure
          size="inherit"
          icon="arrow-head-left"
          class="back-button-text-link"
          aria={{ 'aria-label': 'Back to Menu' }}
          onClick={() => this.sendDataLayerEvent()}
        >
          {this.text}
        </phn-p-button-pure>
      </Host>
    );
  }
}
